	border-radius: 0.25rem;
	border: none;
}

.accordion .card .card-header {
	background-color: transparent;
	border: none;
	padding: 2rem;
}

.card-header:first-child {
	border-radius: calc(0.25rem - 1px) calc(0.25rem - 1px) 0 0;
}

