	font-weight: 400;
	font-size: 1rem;
}

.mb-0,
.my-0 {
	margin-bottom: 0 !important;
}

.accordion .card .card-header a {
	display: block;
	color: inherit;
	text-decoration: none;
	font-size: medium;
	position: relative;
	transition: color 0.5s ease;
	padding-right: 1.5rem;
	font-weight: 700;
}

