	font-weight: 400;
	font-size: 1rem;
}

.accordion .card .card-header a[aria-expanded="false"]:before {
	content: "\f067";
}

.accordion .card .card-header a[aria-expanded="true"]:before {
	content: "\f068";
}

.accordion .card .card-header a:before {
	position: absolute;
	right: 7px;
	top: 0;
	font-size: 18px;
	display: block;
	font-family: FontAwesome;
	display: inline-block;
	padding-right: 3px;
